<template>
  <div class="popup__wrap" :class="{ on: popupEventFlag === 'Open' }">
    <div class="popup__bg"></div>
    <div class="common__popup box__shadow__02">
      <div class="popup__top">
        <h4>코드 {{ popupState }}</h4>
        <button class="pop__close__btn" @click="popupCloseClick"></button>
      </div>
      <ul>
        <li>
          <dl>
            <dt class="f__medium">상위기능</dt>
            <dd>
              <select
                name=""
                id=""
                ref="mainCodeSelector"
                v-model="reqFuncInfo.funcHighId"
              >
                <option value="">상위기능 없음</option>
                <option
                  v-for="func in funcHighIdList"
                  :key="func.index"
                  :value="func.funcId"
                >
                  {{ func.funcNm }}
                </option>
              </select>
            </dd>
          </dl>
        </li>
        <li v-show="popupState === '수정'">
          <dl>
            <dt class="f__medium">기능ID</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model="reqFuncInfo.funcId"
                :disabled="popupState === '수정'"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">기능명</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model="reqFuncInfo.funcNm"
              />
            </dd>
          </dl>
        </li>
        <li class="question__input">
          <dl>
            <dt class="f__medium">설명</dt>
            <dd>
              <textarea
                name=""
                id=""
                class="custom__input"
                v-model="reqFuncInfo.funcDsc"
              ></textarea>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">사용여부</dt>
            <dd>
              <select name="" id="" v-model="reqFuncInfo.useYn">
                <option value="">선택</option>
                <option value="Y">사용</option>
                <option value="N">미사용</option>
              </select>
            </dd>
          </dl>
        </li>
      </ul>
      <div class="custom__btn__wrap">
        <button class="custom__btn delete" @click="removeOrClean">
          <span>
            <img src="@/assets/images/icon/ic-remove.svg" alt="저장 아이콘" />
          </span>
          <p>삭제</p>
        </button>
        <button class="custom__btn" @click="addFunction">
          <span>
            <img src="@/assets/images/icon/ic-save.svg" alt="저장 아이콘" />
          </span>
          <p>저장</p>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import { clear } from "@/utill/commom";
import { addFunction, updateFunction, removeFunction } from "@/api/function";
export default {
  props: {
    popupEventFlag: String,
    popupState: String,
    funcHighIdList: Array,
    selectedObj: [Object, Boolean],
  },
  data() {
    return {
      reqFuncInfo: {
        funcId: "",
        funcNm: "",
        funcDsc: "",
        funcHighId: "",
        useYn: "",
      },
    };
  },
  watch: {
    popupState() {
      if (this.popupState === "수정") {
        this.reqFuncInfo = this.selectedObj;
        if (this.selectedObj.useYn === "사용") this.reqFuncInfo.useYn = "Y";
        else if (this.selectedObj.useYn === "미사용")
          this.reqFuncInfo.useYn = "N";
      } else if (this.popupState === "추가") {
        clear(this.reqFuncInfo);
      }
    },
  },
  methods: {
    popupCloseClick() {
      this.$emit("popupClose", "Close");
    },
    addFunction() {
      if (this.validateComCode()) {
        if (this.$env == "development") console.log(this.reqFuncInfo);
        if (this.popupState === "수정") {
          updateFunction(this.reqFuncInfo)
            .then((response) => {
              if (response.data.status === "OK") {
                this.$toast.success("기능 수정이 완료되었습니다");
                this.popupCloseClick();
                this.$emit("refreshList");
              }
            })
            .catch((error) => this.$toast.error(error.message));
        } else {
          addFunction(this.reqFuncInfo)
            .then((response) => {
              if (this.$env == "development") console.log(response);
              if (response.data.status === "OK") {
                this.$toast.success("가능 추가가 완료되었습니다");
                this.popupCloseClick();
                this.$emit("refreshList");
              }
            })
            .catch((error) => this.$toast.error(error.message));
        }
      }
    },
    validateComCode() {
      /*if (this.reqFuncInfo.funcId === "" || this.reqFuncInfo.funcId === null) {
        this.$toast.error("기능 아이디를 입력하세요.");
        return false;
      }*/
      if (this.reqFuncInfo.funcNm === "" || this.reqFuncInfo.funcNm === null) {
        this.$toast.error("기능 명을 입력하세요.");
        return false;
      }
      if (
        this.reqFuncInfo.funcDsc === "" ||
        this.reqFuncInfo.funcDsc === null
      ) {
        this.$toast.error("기능 설명을 입력하세요.");
        return false;
      }
      if (this.reqFuncInfo.useYn === "" || this.reqFuncInfo.useYn === null) {
        this.$toast.error("사용여부를 선택하세요.");
        return false;
      }
      return true;
    },
    removeOrClean() {
      if (this.popupState === "수정") {
        removeFunction(this.reqFuncInfo.funcId)
          .then((response) => {
            if (this.$env == "development") console.log(response);
            if (response.data.status === "OK") {
              this.$toast.success("기능이 삭제되었습니다");
              this.popupCloseClick();
              this.$emit("refreshList");
            }
          })
          .catch((error) => this.$toast.error(error.message));
      } else if (this.popupState === "추가") {
        this.clear();
      }
    },
    clear() {
      clear(this.reqFuncInfo);
    },
  },
};
</script>
