<template>
  <main class="function__management__main">
    <FunctionPopup
      :popupEventFlag="popupEventFlag"
      @popupClose="popupToggle"
      :popupState="popupState"
      :selectedObj="selectedRowObj"
      @refreshList="getFunctions"
      :funcHighIdList="allFunctionList"
    />
    <section class="box__shadow__01 vertical__scroll grid__wrap">
      <h3 class="page__title">
        {{ this.$store.state.pageInfo["기능 관리"].title }}
      </h3>
      <h5 class="page__sub__title f__regular">
        {{ this.$store.state.pageInfo["기능 관리"].subTitle }}
      </h5>
      <div class="add__or__search__wrap">
        <ul>
          <li>
            <dl>
              <dt>상위기능</dt>
              <dd>
                <select
                  name=""
                  id=""
                  v-model="funcHighIdFilter"
                  @change="gridDataFiltering"
                >
                  <option value="">전체</option>
                  <option
                    v-for="func in allFunctionList"
                    :key="func.index"
                    :value="func.funcId"
                  >
                    {{ func.funcNm }}
                  </option>
                </select>
              </dd>
            </dl>
          </li>
          <li>
            <dl>
              <dt>사용여부</dt>
              <dd>
                <select
                  name=""
                  id=""
                  ref="useYnSelector"
                  v-model="useYnFilter"
                  @change="gridDataFiltering"
                >
                  <option value="">전체</option>
                  <option value="사용">사용</option>
                  <option value="미사용">미사용</option>
                </select>
              </dd>
            </dl>
          </li>
        </ul>
        <button class="custom__btn" @click="popupToggle('추가')" v-if="false">
          <span>
            <img src="@/assets/images/icon/ic-plus.svg" alt="추가 아이콘" />
          </span>
          <p>기능 추가</p>
        </button>
      </div>
      <div class="grid__wrap" @click="rowClick($event)">
        <Grid
          class="on"
          ref="gridEl"
          :cols="cols"
          :pagination="true"
          :sort="true"
          :rows="filteredGridData"
        ></Grid>
      </div>
    </section>
  </main>
</template>
<script>
import "@/assets/css/functionManagement.css";
import { Grid } from "gridjs-vue";
import FunctionPopup from "@/components/SystemManagement/FunctionManagement/FunctionPopup";
import { getFunctionList } from "@/api/function";

export default {
  data() {
    return {
      popupEventFlag: "Close",
      cols: [
        { name: "번호", id: "index" },
        { name: "상위기능", id: "funcHighNm" },
        { name: "기능명", id: "funcNm" },
        { name: "기능ID", id: "funcId" },
        { name: "상태", id: "useYn" },
        { name: "등록자명", id: "registerName" },
        { name: "변경일시", id: "updateDate" },
      ],
      functionList: [],
      allFunctionList: [],
      funcHighIdFilter: "",
      useYnFilter: "사용",
      selectedRowObj: "",
      popupState: "",
      filteredGridDataBack: [],
      filteredGridData: [],
    };
  },
  methods: {
    popupToggle(evtName) {
      if (evtName === "추가") this.selectedRowObj = false;
      this.popupState = evtName;
      this.popupEventFlag === "Close"
        ? (this.popupEventFlag = "Open")
        : (this.popupEventFlag = "Close");
    },
    rowClick(param) {
      let disable = true;
      if (disable) return;
      const selectedObjIndex = Number(param.path[1].childNodes[0].textContent);
      const selectedCell = param.path[0].getAttribute("data-column-id");
      if (selectedCell === "funcId") this.popupToggle("수정");
      this.selectedRowObj = { ...this.functionList[selectedObjIndex - 1] };
    },
    getFunctions() {
      getFunctionList().then((response) => {
        this.functionList = response.data.response.data;
        this.functionList.forEach(function (object, index) {
          object.index = index + 1;
          if (object.useYn === "Y") {
            object.useYn = "사용";
          } else {
            object.useYn = "미사용";
          }
        });
        this.allFunctionList = this.functionList;
        this.filteredGridDataBack = this.functionList;
        if (this.funcHighIdFilter === "" && this.useYnFilter === "사용") {
          this.filteredGridData = this.functionList;
          this.filteredGridData = this.filteredGridData.filter(
            (obj) => obj.useYn.trim() === "사용"
          );
        } else {
          this.gridDataFiltering();
        }
      });
    },
    searchFunctions() {
      let searchQuery = "";
      if (this.funcHighIdFilter !== "") {
        searchQuery += "funcHighId=" + this.funcHighIdFilter;
      }
      if (this.useYnFilter !== "") {
        searchQuery += "&useYn=" + this.useYnFilter;
      }
      getFunctionList(searchQuery).then((response) => {
        this.functionList = response.data.response.data;
        this.functionList.forEach(function (object, index) {
          object.index = index + 1;
          if (object.useYn === "Y") {
            object.useYn = "사용";
          } else {
            object.useYn = "미사용";
          }
        });
      });
    },
    gridDataFiltering() {
      this.filteredGridData = this.filteredGridDataBack;

      if (this.funcHighIdFilter !== "") {
        this.filteredGridData = this.filteredGridData.filter(
          (obj) => obj.funcHighId.trim() === this.funcHighIdFilter
        );
      }
      if (this.useYnFilter !== "") {
        this.filteredGridData = this.filteredGridData.filter(
          (obj) => obj.useYn.trim() === this.useYnFilter
        );
      }
    },
  },
  components: {
    Grid,
    FunctionPopup,
  },
  computed: {
    getFunctionList() {
      return this.functionList;
    },
  },
  created() {
    this.getFunctions();
  },
};
</script>
