import { get, create, update, remove } from "@/utill/getData";

export function getFunctionList(searchQuery) {
  if (searchQuery) {
    return get(`/functions?${searchQuery}`);
  } else {
    return get("/functions");
  }
}

export function addFunction(param) {
  return create("/functions", param);
}

export function updateFunction(param) {
  return update(`/functions/${param.funcId}`, param);
}

export function removeFunction(funcId) {
  return remove(`/functions/${funcId}`);
}
